				"html": "<h2>Introduction</h2>\n<p>The <code>if</code> statement executes a statement if a specified condition is <strong>true</strong>. If the condition is <strong>false</strong>, another statement can be executed using the <code>else</code> statement..</p>\n<blockquote>\n<p><strong>Note:</strong> The <code>else</code> statement is optional.</p>\n</blockquote>\n<pre><code>if (condition)\n   statement1\nelse\n   statement2\n</code></pre>\n<p>Multiple <code>if...else</code> statements can be nested to create an <code>else if</code> clause.</p>\n<pre><code>if (condition1)\n   statement1\nelse if (condition2)\n   statement2\nelse if (condition3)\n   statement3\n...\nelse\n   statementN\n</code></pre>\n<p><em><strong>Note:</strong></em> If you want to execute more than one statement in the <code>if</code>, <code>else</code> or <code>else if</code> part, braces are required around the statements.</p>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Statements/if...else\">MDN link</a> | <a href=\"https://msdn.microsoft.com/en-us/library/85yyde5c.aspx\">MSDN link</a></p>\n<h2>Examples</h2>\n<p><strong>Using</strong> <code>if...else</code>:</p>\n<pre><code>if (x == 5) {\n  z = 7;\n  q = 42;\n}\nelse\n  z = 19;\n</code></pre>\n<p><strong>Using</strong> <code>else if</code>:</p>\n<pre><code>if (x &#x3C; 10)\n  return \"Small number\";\nelse if (x &#x3C; 50)\n  return \"Medium number\";\nelse if (x &#x3C; 100)\n  return \"Large number\";\nelse {\n  flag = 1;\n  return \"Invalid number\";\n}\n</code></pre>",
